<style>
    .textBlue{
        color: #5778f3;
    }
    .pressDown {
        background-color: #5778f3;
        padding: 10px 30px;
        color: #fff;
        border-radius: 5px;
        border-bottom: 4px solid #3e4caf;
        transition: 0.1s;
    }

    .pressDown:hover{
        border-bottom-width: 0;
        margin-top: 3px;
        color: #fff;
    }

    .pressDownDefault {
        background-color: #707070;
        padding: 10px 30px;
        color: #fff;
        border-radius: 5px;
        border-bottom: 4px solid #35363d;
        transition: 0.1s;
    }

